import React from 'react'
import '../../../css/style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'

const percent15 = 0.1287;
const percent28 = 0.2767;
const percent57 = 0.5947;

class CalculaMartingale extends React.Component {

    retorno(){
        let temp = (parseFloat(this.ordemA())*(this.props.newPayout/100))+parseFloat(this.props.newOrdem);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno2(){
        let temp = (parseFloat(this.ordemA2())*(this.props.newPayout/100))+parseFloat(this.retorno());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno3(){
        let temp = (parseFloat(this.ordemA3())*(this.props.newPayout/100))+parseFloat(this.retorno2());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno4(){
        let temp = (parseFloat(this.ordemA4())*(this.props.newPayout/100))+parseFloat(this.retorno3());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno5(){
        let temp = (parseFloat(this.ordemA5())*(this.props.newPayout/100))+parseFloat(this.retorno4());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }   
    ordemA(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(percent15);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA2(){
        let temp = parseFloat(this.retorno()) * parseFloat(percent15);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA3(){
        let temp = parseFloat(this.retorno2()) * parseFloat(percent15);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA4(){
        let temp = parseFloat(this.retorno3()) * parseFloat(percent15);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA5(){
        let temp = parseFloat(this.retorno4()) * parseFloat(percent15);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(percent28);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB2(){
        let temp = parseFloat(this.retorno()) * parseFloat(percent28);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB3(){
        let temp = parseFloat(this.retorno2()) * parseFloat(percent28);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB4(){
        let temp = parseFloat(this.retorno3()) * parseFloat(percent28);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB5(){
        let temp = parseFloat(this.retorno4()) * parseFloat(percent28);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemC(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(percent57);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemC2(){
        let temp = parseFloat(this.retorno()) * parseFloat(percent57);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemC3(){
        let temp = parseFloat(this.retorno2()) * parseFloat(percent57);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemC4(){
        let temp = parseFloat(this.retorno3()) * parseFloat(percent57);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemC5(){
        let temp = parseFloat(this.retorno4()) * parseFloat(percent57);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }

    render(){
            return (
                <div class="tableResultado"> 
                    <table class="tableResultado">
                        <tr>
                            <td>
                                
                            </td>
                            <td class="tituloTabelaLinha">
                                1° Ordem
                            </td>
                            <td class="tituloTabelaLinha">
                                2° Ordem
                            </td>
                            <td class="tituloTabelaLinha">
                                3° Ordem
                            </td>
                            <td class="tituloTabelaLinha">
                                Lucro
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                1° Nível
                            </td>
                            <td>
                                R$ { this.ordemA() }
                            </td>
                            <td>
                                R$ { this.ordemB() }
                            </td>
                            <td>
                                R$ { this.ordemC() }
                            </td>
                            <td>
                                R${ this.retorno() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                2° Nível
                            </td>
                            <td>
                                R$ { this.ordemA2() }
                            </td>
                            <td>
                                R$ { this.ordemB2() }
                            </td>
                            <td>
                                R$ { this.ordemC2() }
                            </td>
                            <td>
                                R$ { this.retorno2() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                3° Nível
                            </td>
                            <td>
                                R$ { this.ordemA3() }
                            </td>
                            <td>
                                R$ { this.ordemB3() }
                            </td>
                            <td>
                                R$ { this.ordemC3() }
                            </td>
                            <td>
                                R${ this.retorno3() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                4° Nível
                            </td>
                            <td>
                                R$ { this.ordemA4() }
                            </td>
                            <td>
                                R$ { this.ordemB4() }
                            </td>
                            <td>
                                R$ { this.ordemC4() }
                            </td>
                            <td>
                                R$ { this.retorno4() }
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                5° Nível
                            </td>
                            <td>
                                R$ { this.ordemA5() }
                            </td>
                            <td>
                                R$ { this.ordemB5() }
                            </td>
                            <td>
                                R$ { this.ordemC5() }
                            </td>
                            <td>
                                R$ { this.retorno5() }
                            </td>
                        </tr>
                    </table>

                    
                </div>
            )
        }
    }

const mapStateToProps = state => ({
    newOrdem:  state.CalculosReducers.newOrdem,
    newPayout:  state.CalculosReducers.newPayout,
})
const mapDispatchToProps = dispatch => bindActionCreators({
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(CalculaMartingale); 