
import React from 'react'
import '../../../css/style.css'
import {connect} from 'react-redux'
import { 
    appCarregando,
    atualizaOrdem, 
    atualizaPayout, 
    atualizaStoploss,
 } from '../../../redux/actions'
import { bindActionCreators } from 'redux'
import CalculaSoros from './CalculaSoros'

class CenaSoros extends React.Component {
    state = {
        loading: false,
        inputOrdem: this.props.newOrdem,
        inputPayout: this.props.newPayout,
        inputStoploss: this.props.newStoploss,  
    };
    inputChangeOrdem = event => {
        this.setState({ inputOrdem: event.target.value })
    }
    inputChangePayout = event => {
        this.setState({ inputPayout: event.target.value })
    }
    atualizaEstado = () => {
        const { atualizaOrdem, atualizaPayout } = this.props;
        {
            this.state.inputOrdem == '' || this.state.inputPayout == ''
            ?   alert('Preencha os campos de: "ORDEM" e "PAYOUT"')
            :   atualizaOrdem(this.state.inputOrdem);
                atualizaPayout(this.state.inputPayout)
        }
    }
    _zerar = () => {
        const { atualizaOrdem, atualizaPayout } = this.props;
        this.setState({ inputOrdem: 0 })
        this.setState({ inputPayout: 0 })
        atualizaOrdem(0);
        atualizaPayout(0);
    }
    render(){
        const { newOrdem, newPayout } = this.props;
        const { inputOrdem, inputPayout } = this.state;
            return (
                <div>
                    <table>
                        <tr>
                            <td>
                                <h3>Informe o valor de sua ordem:</h3>
                                <input
                                    type="number" 
                                    value={inputOrdem}
                                    onChange={this.inputChangeOrdem} />
                                <h4> Ordem: R${ newOrdem } </h4>  
                            </td>

                            <td>
                                <h3>Informe o valor do payout:</h3>
                                <input 
                                    type="number" 
                                    value={inputPayout}
                                    onChange={this.inputChangePayout} />
                                <h4> Payout: { newPayout }% </h4>                            
                            </td>                            
                        </tr>
                        <tr><td colSpan="2">
                            <button 
                                class="botaoCalculo"
                                type='submit'
                                onClick={this.atualizaEstado} > Calcular </button><br />
                        </td></tr>
                        <tr><td height="20px"> </td></tr>
                        <tr><td colSpan="2">
                            <CalculaSoros />
                        </td></tr>
                        <tr><td colSpan="2">
                            <button 
                                class="botaoCalculoZerar"
                                type="submit"
                                onClick={this._zerar} >Zerar</button>
                        </td></tr>
                    </table>

                    
                           
                </div>            
            );
        }
    }

    const mapStateToProps = state => ({
        newOrdem:  state.CalculosReducers.newOrdem,
        newPayout:  state.CalculosReducers.newPayout,
        newStoploss: state.CalculosReducers.newStoploss,  
    })
    const mapDispatchToProps = dispatch => bindActionCreators({        
        appCarregando,
        atualizaOrdem, 
        atualizaPayout, 
        atualizaStoploss,
    }, dispatch);
    export default connect(mapStateToProps, 
        mapDispatchToProps
    )(CenaSoros); 