
import React,  { Component } from 'react'
import '../css/style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import CenaSoros from './cenas/soros/CenaSoros';
import {
    atualizaOrdem
} from '../redux/actions'

class Teste extends Component {
    state = {
        inputOrdem: ''
    }
    inputChangeOrdem = event => {
        this.setState({
            inputOrdem: event.target.value
        })
    }
    atualizaEstado = () => {
        const { atualizaOrdem } = this.props;
        atualizaOrdem(this.state.inputOrdem);
    }
    
    render(){
        return(
            <div>
               <CenaSoros />
            </div>
        );
    }
}
const mapStateToProps = state => ({   
    newOrdem: state.CalculosReducers.newOrdem,
})
const mapDispatchToProps = dispatch => bindActionCreators({ 
    atualizaOrdem,
}, dispatch);
export default connect(mapStateToProps,    
    mapDispatchToProps
)(Teste); 