import { 
    MODIFICA_RETORNO,
    MODIFICA_STOPLOSS,
    CADASTRO_USUARIO_ERRO,
    CADASTRO_USUARIO_SUCESSO,
    AUTENTICA_USUARIO_ERRO,
    AUTENTICA_USUARIO_SUCESSO,
    SAIR,
    CLICK_UPDATE_EMAIL,
    CLICK_UPDATE_NOME,
    CLICK_UPDATE_SENHA,
    RECUPERA_SENHA_SUCESSO,
    RECUPERA_SENHA_ERRO,
    APP_CARREGANDO,
    ATUALIZA_ORDEM,
    ATUALIZA_PAYOUT,
    ATUALIZA_STOPLOSS,
 } from '../actions'

const INITIAL_STATE = {
    carregando: false,
    newNome: '',
    newEmail: '',
    newSenha: '',

    erroLogin:'',
    erroCadastro: '',
    erroRecuperaSenha:'',
    sucessoRecuperaSenha:'',

    redirect:'',
    newBanca:0,
    newPayout:0,
    newStoploss:0,
    newOrdem:0,
    retorno:0,
    resultado:0,    
}

const CalculosReducers = (state = INITIAL_STATE, action) => {
    console.log(action);
    switch(action.type){
        case APP_CARREGANDO:
            return {
                ...state,
                carregando: action.carregando
            } 
        case CLICK_UPDATE_NOME:
            return {
              ...state,
              newNome: action.newNome,
            }
        case CLICK_UPDATE_SENHA:
            return {
              ...state,
              newSenha: action.newSenha
            }
        case CLICK_UPDATE_EMAIL:
            return {
              ...state,
              newEmail: action.newEmail
            }
        case MODIFICA_RETORNO:
            return {
                ...state,
                retorno: action.payload
            }    
        case MODIFICA_STOPLOSS:
            return {
                ...state,
                stopLoss: action.payload
            }
        case CADASTRO_USUARIO_ERRO:
            return {
                ...state,
                erroCadastro: action.payload,
            }
        case CADASTRO_USUARIO_SUCESSO:
            return {
                ...state,
                redirect: 'Login'
            }
        case AUTENTICA_USUARIO_ERRO:
            return {
                ...state,
                erroLogin: action.payload,
            } 
        case AUTENTICA_USUARIO_SUCESSO:
            return {
                ...state,
                redirect: 'Principal'
            }
        case RECUPERA_SENHA_SUCESSO:
            return {
                ...state,
                sucessoRecuperaSenha: action.payload,
            }
        case RECUPERA_SENHA_ERRO:
            return {
                ...state,
                erroRecuperaSenha: action.payload
            }
        case SAIR:
            return {
                ...state,
                email:'',
                senha:'',
                nome:'',
                redirect:'Login',
            }
        case ATUALIZA_ORDEM:
            return {
              ...state,
              newOrdem: action.newOrdem,
            }
        case ATUALIZA_PAYOUT:
            return {
              ...state,
              newPayout: action.newPayout,
            }
        case ATUALIZA_STOPLOSS:
            return {
              ...state,
              newStoploss: action.payload,
            }
        }   
    return state;
}
 
export default CalculosReducers;