import firebase from 'firebase';

export const MODIFICA_STOPLOSS = 'MODIFICA_STOPLOSS';
export const modificaStopLoss = (stopLoss) => {
    return{
        type:'MODIFICA_STOPLOSS',
        payload: stopLoss
    }
}
export const MODIFICA_RETORNO = 'MODIFICA_RETORNO';
export const modificaRetorno = (retorno) => {
    return{
        type:'MODIFICA_RETORNO',
        payload: retorno
    }
}
export const cadastraUsuario = ({ inputEmail, inputSenha }) => {
    return dispatch => {
        firebase.auth().createUserWithEmailAndPassword( inputEmail, inputSenha )
        .then(user => { cadastroUsuarioSucesso(dispatch) })
        .catch(erro => cadastroUsuarioErro(erro, dispatch));
    }
}
export const CADASTRO_USUARIO_SUCESSO = 'CADASTRO_USUARIO_SUCESSO';
const cadastroUsuarioSucesso = (dispatch) => {
    dispatch ({
        type:'CADASTRO_USUARIO_SUCESSO',
    });
}
export const CADASTRO_USUARIO_ERRO = 'CADASTRO_USUARIO_ERRO';
const cadastroUsuarioErro = (erro, dispatch) => {
    let errorMessage = erro.message;
    if(errorMessage == 'The email address is badly formatted.'){
        errorMessage = 'O campo email está vazio ou mal formatado!'
    }
    if(errorMessage == 'The password must be 6 characters long or more.'){
        errorMessage = 'A senha deve ter 6 caracteres ou mais.'
    }
    if(errorMessage == 'There is no user record corresponding to this identifier. The user may have been deleted.'){
        errorMessage = 'Não há registro do email em nosso banco de dados. Verifique o email digitado ou registre-se!'
    }
    if(errorMessage == 'The email address is already in use by another account.'){
        errorMessage = 'Este endereço de email já está em uso!'
    }    
    return dispatch ({
        type:'CADASTRO_USUARIO_ERRO',
        payload: errorMessage
    })
}
export const autenticaUsuario = ({inputEmail, inputSenha}) => {
    return dispatch => {
        firebase.auth().signInWithEmailAndPassword(inputEmail, inputSenha)
        .then(value => autenticaUsuarioSucesso(dispatch) )
        .catch(errorMessage => autenticaUsuarioErro(errorMessage, dispatch));        
    }
}
export const AUTENTICA_USUARIO_SUCESSO = 'AUTENTICA_USUARIO_SUCESSO';
const autenticaUsuarioSucesso = (dispatch) => {
    dispatch ({
        type:'AUTENTICA_USUARIO_SUCESSO'
    });
}
export const AUTENTICA_USUARIO_ERRO = 'AUTENTICA_USUARIO_ERRO';
const autenticaUsuarioErro = (erro, dispatch) => {
    let errorMessage = erro.message;
    if(errorMessage == 'The email address is badly formatted.'){
        errorMessage = 'Preencha o campo email corretamente!'
    }
    if(errorMessage == 'The password is invalid or the user does not have a password.'){
        errorMessage = 'Senha inválida!'
    }
    if(errorMessage == 'There is no user record corresponding to this identifier. The user may have been deleted.'){
        errorMessage = 'Não há registro do email em nosso banco de dados. Verifique o email digitado ou registre-se!'
    }
    dispatch ({
        type:'AUTENTICA_USUARIO_ERRO',
        payload: errorMessage
    });
}
export const recuperaSenha = ({ inputEmail }) => {
    return dispatch => {
        firebase.auth().verifyPasswordResetCode(inputEmail)
        .then(value => recuperaSenhaSucesso(dispatch) )
        .catch(errorMessage => recuperaSenhaErro(errorMessage, dispatch));        
    }
}
export const RECUPERA_SENHA_SUCESSO = 'RECUPERA_SENHA_SUCESSO';
const recuperaSenhaSucesso = (dispatch) => {
    dispatch ({
        type:'RECUPERA_SENHA_SUCESSO'
    });
}
export const RECUPERA_SENHA_ERRO = 'RECUPERA_SENHA_ERRO';
const recuperaSenhaErro = (dispatch) => {
    dispatch ({
        type:'RECUPERA_SENHA_ERRO'
    });
}
export const APP_CARREGANDO = "APP_CARREGANDO";
export const appCarregando = carregando => ({
    type: APP_CARREGANDO,
    carregando
});
export const ATUALIZA_ORDEM = "ATUALIZA_ORDEM";
export const atualizaOrdem = value => ({
    type: ATUALIZA_ORDEM,
    newOrdem: value,
  });
export const ATUALIZA_PAYOUT = "ATUALIZA_PAYOUT";
export const atualizaPayout = value => ({
    type: ATUALIZA_PAYOUT,
    newPayout: value
});
export const ATUALIZA_STOPLOSS = "ATUALIZA_STOPLOSS";
export const atualizaStoploss = value => ({
    type: ATUALIZA_STOPLOSS,
    newStoploss: value
});
export const SAIR = 'SAIR';
export const sair = () => {
    return{
        type:'SAIR'
    }
}
