import React from 'react'
import  '../../css/style.css'
import {connect} from 'react-redux'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom"
import {
    appCarregando
} from '../../redux/actions'
import { bindActionCreators } from 'redux'
import CenaSoros from './soros/CenaSoros'
import CenaSorosGale from './sorosGale/CenaSorosGale'
import CenaMartingale from './martingale/CenaMartingale'

class Principal extends React.Component {
    render(){
        return (        
        <div>   
            <Router>
                <div>
                    <h3>Seja bem vindo { this.props.newNome }, selecione seu gerenciamento:</h3> <br />
                    <div>
                        <table>
                            <tr>
                                <td class="menu botaoMenu"><Link exact to="/">SOROS</Link></td>
                                <td class="menu botaoMenu"><Link to="/sorosGale">SOROSGALE</Link></td>
                                <td class="menu botaoMenu"><Link to="/martingale">MARTINGALE</Link></td>
                            </tr>
                        </table>
                    </div>
                    <Switch>
                        <Route exact path="/">
                            <CenaSoros />
                        </Route>
                        <Route path="/martingale">
                            <Martingale />
                        </Route>
                        <Route path="/sorosGale">
                            <SorosGale />
                        </Route>
                    </Switch>
                </div>
            </Router>
        </div>
        );
    }
  }

const Martingale = () =>{
    return (
        <div>
            <CenaMartingale />
        </div>
    );
}

const SorosGale = () =>{
    return(
        <div>
            <CenaSorosGale />
        </div>
    );
}

const mapStateToProps = state => ({
    newEmail: state.CalculosReducers.newEmail,
    redirect: state.CalculosReducers.redirect,
})
const mapDispatchToProps = dispatch => bindActionCreators({
    appCarregando
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Principal); 