import React from 'react';
import Conteudo from './layout/Conteudo';
import Header from './layout/Header';
import Footer from './layout/Footer';
import '../css/style.css'; 

const Principal = () => {
    return (
        <div class="base principal">
            <table>
                <tr>
                    <td>
                        <Header />                            
                            <Conteudo />                            
                        <Footer />
                    </td>
                </tr>
            </table>                
        </div>
    );
}

export default Principal;