import React from 'react'
import '../../css/style.css' 
import  logo  from '../../img/logoC.png'
import {
    AlteraEmail,
    AlteraSenha,
    AlteraNome,
    autenticaUsuario,
    appCarregando
} from '../../redux/actions'
import {connect} from 'react-redux'
import { Link, Redirect } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import Loading from '../loading'

class Login extends React.Component {
    state = {
            inputEmail: this.props.newEmail,
            inputSenha: '',
            redirect:'',
            loading: false,
    }
    inputChangeEmail = event => {
        this.setState({
          inputEmail: event.target.value,
        })
    }
    inputChangeSenha = event => {
        this.setState({
          inputSenha: event.target.value,
        })
    }
    _autenticaUsuario = async () => {
        const { inputEmail, inputSenha } = this.state;
        const { autenticaUsuario } = this.props;

        this.setState({ loading: true });
        setTimeout( () => {
            this.setState({ loading: false })
        }, 2000 )

        await autenticaUsuario({ inputEmail, inputSenha});
    }

    render(){
        const { loading, inputEmail, inputSenha } = this.state; 
        return (
            
            <div class="login">
                <table class="popUpLogin">
                    <tr><td>
                            <img class="logo" src={logo} />
                    </td></tr>
                    <tr><td>
                            <h1>Área do Aluno</h1>
                    </td></tr>
                    <tr><td>
                            <p>Endereço de E-mail:</p>
                            <input  
                                class="campoLogon"
                                value={inputEmail}
                                onChange={this.inputChangeEmail} />
                    </td></tr>
                    <tr><td>
                            <p>Senha:</p>
                            <input 
                                class="campoLogon" 
                                type="password"
                                value={inputSenha} 
                                onChange={this.inputChangeSenha}
                                />
                    </td></tr>
                    <tr><td>
                        <input
                            class="botaoAction" 
                            type="submit" 
                            value="Entrar" 
                            onClick={this._autenticaUsuario}
                        />                            
                        { 
                            loading 
                            ? <Loading />
                            : <h5 class="erro">{ this.props.erroLogin } </h5> 
                        }
                            { this.atualizaEstado }
                            { this.props.redirect == 'Principal' 
                                ? <Redirect to="/Principal"></Redirect>
                                : <Redirect to="/Login"></Redirect> }
                    </td></tr>
                    <tr><td class="entrarCampo">
                            Ainda não tem registro? <Link to="/Cadastro"> <h3>Registre-se Agora!</h3></Link>
                    </td></tr>
                    <tr><td class="entrarCampo">
                        <Link to="/RecuperaSenha"><h3 class="esqueceuSenha">Esqueceu sua senha? </h3></Link>
                    </td></tr>
                </table>
                
                
            </div>
        );
    }
}

const mapStateToProps = state => ({
    newEmail: state.CalculosReducers.newEmail,
    newSenha: state.CalculosReducers.newSenha,
    erroLogin: state.CalculosReducers.erroLogin,
    redirect: state.CalculosReducers.redirect,
})
const mapDispatchToProps = dispatch => bindActionCreators({
    autenticaUsuario,
    AlteraEmail,
    AlteraSenha,
    AlteraNome,
    appCarregando
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Login); 