import React from 'react'
import '../../css/style.css' 
import  Logo  from '../../img/logoC.png'
import { 
    AlteraNome,
    AlteraEmail,
    AlteraSenha,
    cadastraUsuario,
    appCarregando
} from '../../redux/actions';
import {connect} from 'react-redux'
import { Link, Redirect } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import Loading from '../loading'

class Cadastro extends React.Component {
    state = {
        inputNome:'',
        inputEmail: '',
        inputSenha: '',
        redirect:'',
        loading: false,    
    }
    inputChangeNome = event => {
        this.setState({
          inputNome: event.target.value,
        })
    }
    inputChangeEmail = event => {
        this.setState({
          inputEmail: event.target.value,
        })
    }
    inputChangeSenha = event => {
        this.setState({
          inputSenha: event.target.value,
        })
    }
    _cadastraUsuario = async () => {
        const {inputNome, inputEmail, inputSenha } = this.state;
        const { AlteraNome, AlteraEmail, AlteraSenha, cadastraUsuario } = this.props;

        this.setState({ loading: true });
        setTimeout( () => {
            this.setState({ loading: false })
        }, 2000 )

        await AlteraNome({inputNome});
        await AlteraEmail({inputEmail});
        await AlteraSenha({inputSenha});
        await cadastraUsuario({ inputNome, inputEmail, inputSenha });
    }
    render(){
        const { loading } = this.state; 
        return (
            <div class="base login">
                    <table class="popUpLogin">                        
                        <tr><td>
                                <img class="logo" src={Logo} />
                        </td></tr>
                        <tr><td>
                                <h1>Cadastre-se</h1>
                        </td></tr>
                        <tr><td>
                                <p>Nome:</p>
                                <input 
                                    class="campoLogon" 
                                    value={this.state.inputNome} 
                                    onChange={this.inputChangeNome}/>
                        </td></tr>
                        <tr><td>
                                <p>Endereço de E-mail:</p>
                                <input 
                                    class="campoLogon" 
                                    value={this.state.inputEmail} 
                                    onChange={this.inputChangeEmail}/>
                        </td></tr>
                        <tr><td>
                                <p>Senha:</p>
                                <input 
                                    class="campoLogon" 
                                    type="password" 
                                    value={this.state.inputSenha} 
                                    onChange={this.inputChangeSenha}/>
                        </td></tr>
                        <tr><td>                                
                                <input 
                                    class="botaoAction" 
                                    type="submit" 
                                    value="Cadastrar" 
                                    onClick={this._cadastraUsuario} />
                                { 
                                    loading 
                                    ? <Loading />
                                    : <h5 class="erro">{ this.props.erroCadastro } </h5> 
                                }
                                { this.props.redirect == 'Login' 
                                ? <Redirect to="/CadastroSucesso"></Redirect>
                                : <Redirect to="/Cadastro"></Redirect> }
                        </td></tr>
                        <tr><td>
                            <Link to="/Login"> <h3>Volte ao Login </h3></Link>
                        </td></tr>
                    </table>
            </div>
        );
    }
}
const mapStateToProps = state => ({
    newNome: state.CalculosReducers.newNome,
    newEmail: state.CalculosReducers.newEmail,
    newSenha: state.CalculosReducers.newSenha,
    erroCadastro: state.CalculosReducers.erroCadastro,
    redirect: state.CalculosReducers.redirect,
})
const mapDispatchToProps = dispatch => bindActionCreators({
    AlteraNome,
    AlteraEmail,
    AlteraSenha,
    cadastraUsuario,
    appCarregando
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Cadastro); 